import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchAllProducts,fetchAllProductsByFilters,fetchbrand,fetchcategory,fetchProductsbyid,updateProduct,createProduct } from './productAPI';
const initialState = {
  products: [],
  brand:[],
  category:[],
  id:null,
  totalItems:100,
  status: 'idle'
};
export const fetchAllProductsAsync = createAsyncThunk(
  'product/fetchAllProducts',
  async () => {
    const response = await fetchAllProducts();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const createProductAsync = createAsyncThunk(
  'product/create',
  async (product) => {
    const response = await createProduct(product);
    return response.data;
  }
);

export const updateProductAsync = createAsyncThunk(
  'product/update',
  async (update) => {
    console.log(update);
    const response = await updateProduct(update);
    console.log(response.data);
    return response.data;
  }
);
export const fetchcategoryAsync= createAsyncThunk(
  'product/fetchcategory',
  async () => {
    const response = await fetchcategory();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchbrandAsync = createAsyncThunk(
  'product/fetchbrand',
  async () => {
    const response = await fetchbrand();
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchAllProductsByFiltersAsync = createAsyncThunk(
  'product/fetchAllProductsByFilters',
   async ({filter,sort,pagination,admin}) => {
    console.log(pagination);
    const response = await fetchAllProductsByFilters(filter,sort,pagination,admin);
    // The value we return becomes the `fulfilled` action payload
    console.log(response.data.products);
    return (response.data.products);
  }
);
export const fetchProductsbyidAsync = createAsyncThunk(
  'product/fetchProductsbyid',
   async (id) => {
   console.log(id);
    const response = await fetchProductsbyid(id);
    // The value we return becomes the `fulfilled` action payload
    console.log(response.data);
    return (response.data);
  }
);
export const productSlice = createSlice({
  name: 'product',
  initialState,
  reducers: {
    clearSelectedProduct:(state)=>{
      state.id = null
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchAllProductsAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllProductsAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products = action.payload;
      })
      .addCase(fetchAllProductsByFiltersAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchAllProductsByFiltersAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products= action.payload;
        // state.totalitem=action.payload.totalitem;
      })
      .addCase(fetchcategoryAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchcategoryAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.category= action.payload;
        // state.totalitem=action.payload.totalitem;
      })
      .addCase(fetchbrandAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchbrandAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.brand= action.payload;
        // state.totalitem=action.payload.totalitem;
      })
      .addCase(fetchProductsbyidAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchProductsbyidAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.id= action.payload;
        // state.totalitem=action.payload.totalitem;
      })
      .addCase(createProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(createProductAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.products.push(action.payload);
      })
      .addCase(updateProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateProductAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index = state.products.findIndex(
          (product) => product.id === action.payload.id
        );
        state.products[index] = action.payload;
      });
  },
});
export const selectAllProducts = (state) => state.product.products;
export const selectAllbrand = (state) => state.product.brand;
export const selectAllcategory = (state) => state.product.category;
export const selectproductbyid=(state)=>state.product.id;
export const { clearSelectedProduct } = productSlice.actions;
// export const selecttotalitem = (state) => state.product.totalitem;
export default productSlice.reducer;