import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { orderCount ,updatedOrder,fetchAllOrders} from './orderApi';
const initialState = {
  orders:[],
  currentorder:null,
  status: "idle",
}

  export const orderCountAsync= createAsyncThunk(
    'order/orderCount',
    async (amount) => {
      console.log(amount);
      const response = await  orderCount(amount);
      // The value we return becomes the `fulfilled` action payload
      console.log(response.data);
      return response.data;
    }
  );
  export const updateOrderAsync = createAsyncThunk(
    'order/updateOrder',
    async (order) => {
      const response = await updatedOrder(order);
      // The value we return becomes the `fulfilled` action payload
      return response.data;
    }
  );
  
  export const fetchAllOrdersAsync = createAsyncThunk(
    'order/fetchAllOrders',
    async ({sort, pagination}) => {
      const response = await fetchAllOrders(sort,pagination);
      // The value we return becomes the `fulfilled` action payload
      console.log(response.data)
      return response.data;
    }
  );
  export const orderSlice = createSlice({
    name: 'order',
    initialState,
    reducers: {
      resetOrder: (state) => {
        state.currentorder =null;
      },
    },
    extraReducers: (builder) => {
      builder
        .addCase(orderCountAsync.pending, (state) => {
          state.status = 'loading';
        })
        .addCase(orderCountAsync.fulfilled, (state, action) => {
          state.status = 'idle';
          state.orders.push(action.payload);
          state.currentorder=action.payload;
        })
        .addCase(fetchAllOrdersAsync.pending, (state) => {
          state.status = 'loading';
        })
        .addCase(fetchAllOrdersAsync.fulfilled, (state, action) => {
          state.status = 'idle';
          state.orders = action.payload.orders;
          state.totalOrders = action.payload.totalOrders;
        })
        .addCase(updateOrderAsync.pending, (state) => {
          state.status = 'loading';
        })
        .addCase(updateOrderAsync.fulfilled, (state, action) => {
          state.status = 'idle';
          const index =  state.orders.findIndex(order=>order.id===action.payload.id)
          state.orders[index] = action.payload;
        })
    },
  });

  export const {resetOrder}=orderSlice.actions;
  export default orderSlice.reducer;
  export const selectOrders = (state) => state.order.orders;
  export const selectTotalOrders = (state) => state.order.totalOrders;
export const selectcurrentorder=(state)=>state.order.currentorder;