export const CreateUser = (userData) => {
  return new Promise((resolve, reject) => {
    (async () => {
      try {
          
        const response = await fetch('/auth/Signup', {
          method: 'POST',
          body: JSON.stringify(userData),
          headers: { 'content-type': 'application/json' },
        });
        const data = await response.json();
        console.log(data);
        resolve({ data});
      } catch (error) {
        reject(error);
      }
})();
});
};

