import "./App.css"
import Home from "./page/Home"
import Loginpage from "./page/LoginPage"
import SignupPage from "./page/SignupPage"
import CartPage from "./page/CartPage"
import Checkout from "./page/Checkout"
import * as React from "react";
import Protected from "./features/auth/components/protected"
import ProductdetailPage from "./page/ProductdetailPage";
import StripeCheckout from './page/StripeCheckout';
import {fetchItemByUserIdAsync} from "./features/cart/cartSlice";
import { useEffect } from "react"
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";
import { useDispatch, useSelector } from "react-redux"
import {checkAuthAsync, selectLoggedInuser , selectUserChecked} from "./features/auth/AuthSlice"
import Errorpage  from "./page/Errorpage"
import Ordersuccsesspage from "./page/Ordersuccsesspage"

import UserOrderPage from "./page/Userorderpage"
import UserProfilePage from "./page/UserProfilePage"
import { fetchLoggedInUserAsync } from "./features/user/userSlice"
import Logout from "./features/auth/components/Logout"
import Forgotpasswordpage from "./page/Forgotpasswordpage"
import AdminHomePage from "./page/AdminHomePage"
import AdminProductFormPage from "./page/AdminProductFormPage"
import AdminOrderPage from "./page/AdminOrderPage"
import AdminProductDetailPage from "./page/AdminProductDetailPage"
import ProtectedAdmin from "./features/auth/components/Protectedadmin"
const router = createBrowserRouter([
  {
    path: "/",
    element: <Protected><Home> </Home></Protected>,
  },
  {
    path: "/admin",
    element: <ProtectedAdmin>
      <AdminHomePage></AdminHomePage>
    </ProtectedAdmin>
  },
  {
    path: "/Login",
    element: <Loginpage></Loginpage>,
  },
  {
    path: "/Signup",
    element: <SignupPage></SignupPage>,
  },
  {
    path: "/Cart",
    element:<Protected><CartPage>
    </CartPage>
    </Protected> 
  },
  {
    path: "/Checkout",
    element: <Protected><Checkout></Checkout></Protected>
  },
  {
    path: "/Productdetail/:id",
    element: <Protected><ProductdetailPage></ProductdetailPage></Protected>
  },
  {
    path: "/order-success/:id",
    element: <Ordersuccsesspage></Ordersuccsesspage>
  },
  {
    path: "/orders",
    element: <UserOrderPage></UserOrderPage>
  },
  {
    path: "/profile",
    element: <UserProfilePage></UserProfilePage>
  },
  {
    path: "/forgotpassword",
    element: <Forgotpasswordpage></Forgotpasswordpage>
  },
  {
    path: "/Logout",
    element: <Logout></Logout>
  },
  {
    path: '/admin/product-detail/:id',
    element: (
      <ProtectedAdmin>
        <AdminProductDetailPage></AdminProductDetailPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/stripe-checkout/',
    element: (
      <Protected>
        <StripeCheckout></StripeCheckout>
      </Protected>
    ),
  },
  {
    path: '/admin/product-form',
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage></AdminProductFormPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/admin/Orders',
    element: (
      <ProtectedAdmin>
        <AdminOrderPage></AdminOrderPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/admin/product-form/edit/:id',
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage></AdminProductFormPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: "*",
    element: 
       <Errorpage></Errorpage>
  },
]);
const App = () => {
  const userChecked = useSelector(selectUserChecked);


  const user =useSelector(selectLoggedInuser);
  const dispatch=useDispatch();
  console.log(user);
  console.log("bhavehs");
  useEffect(()=>{
    dispatch(checkAuthAsync())
  },[dispatch])
  useEffect(()=>{
    if(user){
      console.log("arihant1");
    dispatch(fetchItemByUserIdAsync());
    dispatch(fetchLoggedInUserAsync());
    }
  },[dispatch, user]);

  return (
    <div className="App">
      {userChecked&&
        <RouterProvider router={router} />
      }
    </div>
  )
}

export default App
