export const fetchLoggedInUserOrders = () => {
  return new Promise((resolve, reject) => {
    (async () => {
      try {
        const response = await fetch('/orders/own/');
        const data = await response.json();
        resolve({ data});
      } catch (error) {
        reject(error);
      }
})();
});
};
