import  { Fragment, useState } from "react"

import { useDispatch, useSelector } from "react-redux"
import {
 
  DeleteToCartAsync,
  UpdateToCartAsync,
  increment,
  incrementAsync,
  selectItem,
  
} from "../features/cart/cartSlice"
import { useForm } from 'react-hook-form';

import { Dialog, DialogPanel, DialogTitle, Transition, TransitionChild } from '@headlessui/react'
import { XMarkIcon } from '@heroicons/react/24/outline'
import { Link } from "react-router-dom"
import {discountedPrice} from "../app/constant"
import { Navigate } from "react-router-dom"
import {  updateUserAsync} from "../features/user/userSlice"
import { orderCountAsync, selectcurrentorder } from "../features/order/orderSlice";
import { selectUserInfo } from "../features/user/userSlice";

function Checkout(){
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();
  const dispatch = useDispatch()
  const user=useSelector(selectUserInfo);
  console.log(user);

  const [open, setOpen] = useState(true)
  const item=useSelector(selectItem)
  const items=useSelector(selectItem);
   const totalamount=items.reduce((amount,items)=>items.quantity*discountedPrice(items.product)+amount,0);
   const totalquantity=items.reduce((total,items)=>items.quantity+total,0); 
   console.log(totalquantity);
const [selectaddresses,setselectaddresses]=useState(null);
const [paymentmethod,setpaymentmethod]=useState('cash');
   const handleUpdate=(e,items)=>{
    dispatch(UpdateToCartAsync({id:items.id,quantity :+e.target.value}));
   }
   const handleaddress=(e)=>{
       setselectaddresses(user.addresses[e.target.value]);
   }
   const handleorder=()=>{
    const orders={items,totalamount,totalquantity,user:user.id,paymentmethod,selectaddresses,status:'pending'}
          dispatch(orderCountAsync(orders))
   }
   const handlepayment=(e)=>{
    setpaymentmethod(e.target.value);
   }
   const handledelete=(e,id)=>{
    console.log(id);
    dispatch(DeleteToCartAsync(id));
   }
   const currentorder=useSelector(selectcurrentorder);
   console.log(currentorder);
    return (
      <>
    {!(item.length) && <Navigate to='/' replace={true}></Navigate>}
    {currentorder &&currentorder.paymentmethod ==='Cash' && <Navigate to={`/order-success/${currentorder.id}`} replace={true}></Navigate>}
    {currentorder &&  currentorder.paymentmethod ==='Card' && (
        <Navigate
          to={`/stripe-checkout/`}
          replace={true}
        ></Navigate>
      )}

        <div className="mx-auto max-w-7xl px-4 py-25 sm:px-6 lg:px-8">
        <div className="grid grid-cols-1 gap-x-8 gap-y-8 lg:grid-cols-5">
             <div className="lg:col-span-3" >
            <form className="bg-white px-6 py-14"
             noValidate
             onSubmit={handleSubmit((data) => {
               console.log(data);
               dispatch(
                updateUserAsync({
                  ...user,
                  addresses: [...user.addresses,data],
                })
              );
              
             })} >
            <div className="space-y-12">
           <div className="border-b border-gray-900/10 pb-12">
           <h1 className="text-4xl font-bold tracking-tight text-gray-900">Personal Information</h1>
          <p className="mt-1 text-sm leading-6 text-gray-600">Use a permanent address where you can receive mail.</p>

          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-4 sm:grid-cols-6">
            <div className="sm:col-span-4">
              <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                full name
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register('name', {
                    required: 'name is required',
                  })}
                  id="name"
                  autoComplete="given-name"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

          

            <div className="sm:col-span-4">
              <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                Email address
              </label>
              <div className="mt-2">
                <input
                  id="email"
                  {...register('email', {
                    required: 'email is required',
                  })}
                  type="email"
                  autoComplete="email"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-3">
              <label htmlFor="Phone" className="block text-sm font-medium leading-6 text-gray-900">
              Phone NO.
              </label>
              <div className="mt-2">
                <select
                  id="Phone"
                  {...register('Phone', {
                    required: 'phone is required',
                  })}
                  type="tel"
                  autoComplete="phone"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                >
                  <option>United States</option>
                  <option>Canada</option>
                  <option>Mexico</option>
                </select>
              </div>
            </div>

            <div className="col-span-full">
              <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
                Street address
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register('street-address', {
                    required: 'street-address is required',
                  })}
                  id="street-address"
                  autoComplete="street-address"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2 sm:col-start-1">
              <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                City
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register('city', {
                    required: 'city is required',
                  })}
                  id="city"
                  autoComplete="address-level2"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2">
              <label htmlFor="region" className="block text-sm font-medium leading-6 text-gray-900">
                State / Province
              </label>
              <div className="mt-2">
                <input
                  type="text"
                 {...register('region', {
                    required: 'region is required',
                  })}
                  id="region"
                  autoComplete="address-level1"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>

            <div className="sm:col-span-2">
              <label htmlFor="pincode" className="block text-sm font-medium leading-6 text-gray-900">
                ZIP /pincode
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  {...register('pincode', {
                    required: 'pincode is required',
                  })}
                  id="pincode"
                  autoComplete="pincode"
                  className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                />
              </div>
            </div>
          </div>
        </div>
        <div className="mt-6 flex items-center justify-end gap-x-6">
        <button type="button" className="text-sm font-semibold leading-6 text-gray-900">
          Reset
        </button>
        <button
          type="submit"
          className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
        >
        Add Address
        </button>
      </div>

        <div className="border-b border-gray-900/10 pb-12">

          <div className="mt-10 space-y-10">
            <fieldset>
              <legend className="text-sm font-semibold leading-6 text-gray-900">Address</legend>
              <p className="mt-1 text-sm leading-6 text-gray-600">choose from existing address</p>
              <ul role="list" className="divide-y divide-gray-100">
      {user.addresses.map((address,index) => (
        <li key={index} className="flex justify-between gap-x-6 px-5 py-5">
               
          <div className="flex min-w-0 gap-x-4">
          <input
                    id="address"
                    name="address"
                     value={index}
                    onChange={handleaddress}
                    type="radio"
                    className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                  />
            <div className="min-w-0 flex-auto">
              <p className="text-sm font-semibold leading-6 text-gray-900">{address.name}</p>
              <p className="mt-1 truncate text-xs leading-5 text-gray-500">{address.email}</p>
            </div>
          </div>
          <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
            <p className="text-sm leading-6 text-gray-900">{address.pincode}</p>
          </div>
        </li>
      ))}
    </ul>
              <div className="mt-6 space-y-6">
                <div className="flex items-center gap-x-3">
                  <input
                    id="payment-method"
                    name="payment-method"
                    type="radio"
                    value="Cash"
                    checked={paymentmethod==="Cash"}
                    onChange={handlepayment}
                    className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                  />
                  <label htmlFor="push-everything" className="block text-sm font-medium leading-6 text-gray-900">
                      Cash
                  </label>
                </div>
                <div className="flex items-center gap-x-3">
                  <input
                    id="payment-method"
                    name="payment-method"
                    type="radio"
                    checked={paymentmethod==="Card"}
                    value="Card"
                    onChange={handlepayment}
                    className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                  />
                  <label htmlFor="push-email" className="block text-sm font-medium leading-6 text-gray-900">
                      Card
                  </label>
                </div>
              </div>
            </fieldset>
          </div>
        </div>
      </div>
            </form>
           </div>
    <div className="lg:col-span-2">
    <div className="mx-auto bg-white max-w-7xl px-0 mt-16 -py-1 sm:px-0 lg:px-8">
      
      <h1 className="text-4xl font-bold tracking-tight text-gray-900">
        Cart
            </h1>
                    <div className="mt-8">
                        <div className="flow-root">
                          <ul role="list" className="-my-6 divide-y divide-gray-200">
                            {items.map((product) => (
                              <li key={product.id} className="flex py-6">
                                <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-white-200">
                                  <img
                                    src={product.product.thumbnail}
                                    alt={product.product.title}
                                    className="h-full w-full object-cover object-center"
                                  />
                                </div>

                                <div className="ml-4 flex flex-1 flex-col">
                                  <div>
                                    <div className="flex justify-between text-base font-medium text-gray-900">
                                      <h3>
                                        <a href={product.th}>{product.product.title}</a>
                                      </h3>
                                      <p className="ml-4">${discountedPrice(product.product)}</p>
                                    </div>
                                    <p className="mt-1 text-sm text-gray-500">{product.product.brand}</p>
                                  </div>
                                  <div className="flex flex-1 items-end justify-between text-sm">
                                    <div className="text-gray-500">
                                    <label htmlFor="quantity" className="inline mr-5 text-sm font-medium leading-6 text-gray-900">
                                            Qty
                                   </label>
                                      
                                    <select onChange={(e)=>handleUpdate(e,product)} value={product.quantity}>
                                      
                                      <option value="1">1</option>
                                      <option value="2">2</option>
                                      <option value="3">3</option>
                                      <option value="4">4</option>
                                      <option value="5">5</option>
                                    </select>
                                    </div>

                                    <div className="flex">
                                      <button
                                      onClick={e=>handledelete(e,product.id)}
                                        type="button"
                                        className="font-medium text-indigo-600 hover:text-indigo-500"
                                      >
                                        Remove
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              </li>
                            ))}
                          </ul>
                        </div>
                      </div>
                

                    <div className="border-t border-gray-200  px-4 py-6 sm:px-6">
                      <div className="flex justify-between text-base font-medium text-gray-900" >
                        <p>Subtotal</p>
                        <p mr-0>${totalamount}</p>
                      </div>
                      <div className="flex justify-between text-base font-medium text-gray-900" >
                        <p>Quantity</p>
                        <p mr-0>{totalquantity}</p>
                      </div>
                      <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>
                      
                      <div className="mt-6">
                        
                        <div
                          onClick={handleorder}
                          className="flex items-center cursor-pointer justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                        >
                          order now
                        </div>
                        
                      </div>
                      <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                        <p>
                          or{' '}
                          <Link to="/">
                          <button
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                            onClick={() => setOpen(false)}
                          >
                            Continue Shopping
                            <span aria-hidden="true"> &rarr;</span>
                          </button>
                          </Link> 
                        </p>
                      </div>
                    </div>
        
      </div>
    
    </div>
        </div>
        </div>

    </>
     );
}
export default Checkout
