import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addToCart, fetchItemByUserId, UpdateToCart  ,DeleteToCart,ResetCart} from "./cartAPI";

// Initial state of the slice
const initialState = {
  items: [],
  status: 'idle',
  cartLoaded: false
};

// Async thunks
export const addToCartAsync = createAsyncThunk(
  'cart/addToCart',
  async (userData) => {
    const response = await addToCart(userData);
    return response.data;
  }
);
export const  UpdateToCartAsync = createAsyncThunk(
  'cart/ UpdateToCart ',
  async (userData) => {
    console.log(userData.id);
    const response = await  UpdateToCart(userData);
    return response.data;
  }
);

export const  DeleteToCartAsync = createAsyncThunk(
  'cart/ DeleteToCart ',
  async (id) => {
    console.log(id);
    const response = await  DeleteToCart(id);
    return response.data;
  }
);
export const fetchItemByUserIdAsync = createAsyncThunk(
  'cart/fetchItemByUserId',
  async () => {
    const response = await fetchItemByUserId();
    console.log(response.data);
    return response.data;
  }
);
export const ResetCartAsync = createAsyncThunk(
  'cart/ResetCart',
  async () => {
    
    const response = await ResetCart();
    return response.data;
  }
);
// Create slice
const cartSlice = createSlice({
  name: 'cart',
  initialState,
  reducers: {
    increment: (state) => {
      state.value += 1; // There is no value property in the initialState, consider removing this if not used in the state
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(addToCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addToCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items.push(action.payload);
      })
      .addCase(fetchItemByUserIdAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchItemByUserIdAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items = action.payload;
        state.cartLoaded = true;
      })
      .addCase(fetchItemByUserIdAsync.rejected, (state, action) => {
        state.status = 'idle';
        state.cartLoaded = true
      })
      .addCase(UpdateToCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(UpdateToCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index=state.items.findIndex(item=>item.id===action.payload.id);
        state.items[index] = action.payload;
      })
      .addCase(DeleteToCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(DeleteToCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index =  state.items.findIndex(item=>item.id===action.payload.id)
        state.items.splice(index,1);
      })
      .addCase(ResetCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(ResetCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items = [];
      });
  },
});

// Selectors
export const selectItem = (state) => state.cart.items;
export const selectCartLoaded = (state) => state.cart.cartLoaded;
export default cartSlice.reducer;